
<template id="boton_ui">

    <button class="button" @click="press(simbolo)" >{{simbolo}}</button>

</template>

<script>



export default {
  name: 'boton',
  props: {
      simbolo: String
  },
  data:function(){
      return {
          store: this.$store
      }
  },
  methods:{
      press(input){
          if(!isNaN(input)){
            this.store.commit('operar', input)
          } else if(isNaN(input)){
              if((input != 'C') && (input != '=')){
                this.store.commit('operar', input)
              }else if (input == '='){
                  this.store.commit('resultado')
              }else if (input == 'C'){
                  this.store.state.resultado= 0
                  this.store.state.operacion= ''
              }
          }
      }
  }
  
  
}
</script>

