import Vue from 'vue'
import App from './App.vue'
import Vuex from 'vuex'

Vue.use(Vuex)
Vue.config.productionTip = false

const store = new Vuex.Store({
    state: {
        operacion: '',
        resultado: 0
    },
    mutations: {
        operar(state, valor) {
            state.operacion += valor
        },
        resultado(state) {
            state.resultado = eval(state.operacion)
        }
    }
})

new Vue({
    render: h => h(App),
    store:store
}).$mount('#app')
